:root {
  --font-body: "Rubik", sans-serif;
  --fontsize-body: 1.125rem;
}

:root {
  --softblue: hsl(231, 69%, 60%);
  --softred: hsl(0, 94%, 66%);
  --grayishblue: hsl(229, 8%, 70%);
  --verylightgray: hsl(0, 0%, 75%);
  --dimgray: hsl(0, 0%, 41%);
  --verydarkblue: 229 31% 21%;
  --verydarkbluehsl: hsl(229 31% 21%);
  --white: hsl(100, 100%, 100%);
  --black: hsl(0, 0%, 0%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
  font-size: var(--fontsize-body);
  background-color: var(--white);
  font-weight: 300;
}

h1 {
  margin-top: 0;
  line-height: 1.1;
  font-weight: 500;
  font-size: 1.75rem;
  text-align: center;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 2.5rem;
  }
}

h2 {
  margin-top: 0;
  line-height: 1.1;
  font-weight: 500;
  font-size: 1.5rem;
  text-align: center;
}
@media (min-width: 71.875em) {
