  }
}

h3 {
  margin-top: 0;
  margin-bottom: 10px;
  line-height: 1.1;
  font-weight: 500;
  font-size: 1rem;
  text-align: center;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

p {
  color: var(--grayishblue);
  text-align: center;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.5;
  margin: 0;
}

.container {
  padding: 0 2rem;
