.header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2.5rem 0;
}
@media (min-width: 71.875em) {
  .header__nav--links {
    display: flex;
    align-items: center;
    gap: 2.875rem;
    text-transform: uppercase;
    color: black;
    font-weight: 400;
    font-size: 0.875rem;
    letter-spacing: 0.125rem;
  }
  .header__nav--links a {
    color: var(--black);
  }
  .header__nav--links a:hover {
    color: var(--softred);
    transition: color 300ms ease-in-out;
  }
}
@media (min-width: 71.875em) {
  .header__nav--button {
    background-color: var(--softred);
