.mobileMenu {
  position: fixed;
  z-index: 1;
  right: 0;
  top: 0;
  height: 100vh;
  width: 100vw;
  padding: 2.5rem 2rem;
  background: hsl(var(--verydarkblue)/0.95);
  transform: translateX(100%);
  transition: transform 700ms ease-in-out;
}
.mobileMenu__nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.mobileMenu__links {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1.875rem 0;
}
.mobileMenu__links a {
  width: 100%;
  text-align: center;
  text-transform: uppercase;
  color: var(--verylightgray);
  font-size: 1.125rem;
  font-weight: 400;
  border-top: 0.0625rem solid var(--dimgray);
  letter-spacing: 0.0625rem;
  padding: 1.4375rem 0;
}
.mobileMenu__links a :last-child {
  color: var(--white);
}
.mobileMenu__links a:hover {
  color: var(--white);
  transition: all 300ms ease-in-out;
}
.mobileMenu__socials {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2.5rem;
  padding-top: 4.375rem;
}
.mobileMenu__socials a {
  position: relative;
}
.mobileMenu__socials a::before {
  content: "";
  display: block;
  position: absolute;
  height: 0.125rem;
  width: 100%;
  left: 0;
  right: 1.5rem;
  bottom: -0.5rem;
  background: #fff;
  opacity: 0;
  transition: opacity 200ms ease-in-out;
}
.mobileMenu__socials a:hover::before {
  opacity: 1;
}
.mobileMenu__login-button {
  border: 0.125rem solid var(--white);
  border-radius: 0.3125rem;
  padding: 0.5rem 0;
}

.mobileMenu__open {
  transform: translateX(0%);
}

.mobileMenu__noScroll {
  overflow-y: hidden;
}

